module.exports = [{"web-scraper-order":"1534207680-259","name":"Castle Wall","description":"Bolts in this area are not well maintained. Unsafe to climb.","approachpics":false,"approach":["Please Contact a guide and consult local climbers before attempting to access this area."],"topos":[5,8,16],"safe":false,"preApproach":["Muai Thai","Cave wall"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207635-241","name":"Cave wall","description":"Take a slight left as your approach Wees Present and youll be faced with Cave Wall. There are two routes located to the left of the cave opening.","approachpics":true,"approach":["Head to the south end of Railay West beach. At high tide, water level can get quite high, but it's still passable.","At the end of the beach you'll find a narrow path leading up to the left.","Follow this path.","You'll need to climb up over some boulders.","Cave wall is on your left at this point, and Wee's Present is on the right."],"topos":[1],"safe":true,"preApproach":[],"approachTime":"5","warnings":{"bolt":2,"mosquitos":1,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207680-261","name":"Diamond Cave","description":"The close proximity of routes makes this crag a little tricky to navigate. However, it is a favourite among climbing schools.","approachpics":false,"approach":["Take the main road from east railay beach to west railay. You'll see a road leading off to your right.","Follow this road for 5 minutes.","You'll pass the Diamond cave visitor center on your left. Continue following the road.","Shortly after passing this, you'll see a grassy path up to the left leading to the wall."],"topos":[1],"safe":true,"preApproach":[],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207680-262","name":"Diamond Cave North Face","description":"The close proximity of routes makes this crag a little tricky to navigate. However, it is a favourite among climbing schools.","approachpics":false,"approach":["Take the main road from east railay beach to west railay. You'll see a road leading off to your right.","Follow this road for 5 minutes.","You'll pass the Diamond cave visitor center on your left. Continue following the road.","Shortly after passing this, you'll see a grassy path up to the left leading to the wall."],"topos":[1],"safe":true,"preApproach":[],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207685-260","name":"Duncans Boot","description":"Although this crag has easy approach it is easy to miss from the beach. It offers an array of excellent, steep overhang climbs.","approachpics":false,"approach":["Continue along the beach for two minutes","You'll get to a rocky path up to your right.","Follow this path, you'll need to squeeze through a hole in the rock here.","Keep walking through","Turning left here will take you to to Kochel Rock. Duncans Boot area is in the clearing on your right."],"topos":[1,3,6],"safe":true,"preApproach":["Muai Thai","One-Two-Three"],"approachTime":"5","warnings":{"bolt":3,"mosquitos":1,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207651-247","name":"Happy Island","description":"Only accessible in very low-tide, the left side of Happy Island offers excellent overhang and vertical climbs. ","approachpics":false,"approach":["Walk out to the beach and take a right.","At low tide you can walk out to the island here."],"topos":[1],"safe":true,"preApproach":["Phra-Nang Beach"],"approachTime":"15","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":4}},{"web-scraper-order":"1534207682-259","name":"Hidden World Wall","description":"As the name suggests, this is not an easy crag to find. However, once you do you'll be met with fun climbs on excellent orange rock.","approachpics":false,"approach":["Head up the obvious path immediately to the left of Duncans Boot","You'll get to a jungle area, for hidden world, scramble up to the right. Take a left to get to Jungle Gym & The Keep","Scramble up this slope.","At the top of the hill, you'll spot hidden world in the clearing below."],"topos":[1,5,7],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"10","warnings":{"bolt":3,"mosquitos":2,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207663-252","name":"Hueco Wall","description":"Bolts in this area are not well maintained. Unsafe to climb.","approachpics":false,"approach":["Please Contact a guide and consult local climbers before attempting to access this area."],"topos":[5,8,16],"safe":false,"preApproach":["Muai Thai","Cave wall"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207680-258","name":"Jungle Gym","description":"Located up past the trail to Duncan's Boots, Jungle Gym contains sharp and often dirty/greasy climbing. Best not to whip our your newest pair of kicks for this crag.","approachpics":false,"approach":["Scramble up the slope on the left to get to Jungle Gym."],"topos":[1,5],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"15","warnings":{"bolt":3,"mosquitos":2,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207676-257","name":"Jungle Gym South Face","description":"Located up past the trail to Duncan's Boots, Jungle Gym contains sharp and often dirty/greasy climbing. Best not to whip our your newest pair of kicks for this crag.","approachpics":false,"approach":["Accessable at low/medium tide. Head to the south end of Railay east beach. At high tide, this path can get flooded, but is still passable.","When you're almost at the cliff face, continue straight towards the beach.","The big wall on your left is Muai Thai, continue along the beach just past it.","The wall should now be on your right, if you've left this beach section you've gone too far."],"topos":[1],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot","Jungle Gym"],"approachTime":"15","warnings":{"bolt":3,"mosquitos":2,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207665-253","name":"Jurassic Park Wall","description":"Bolts in this area are not well maintained. Unsafe to climb.","approachpics":false,"approach":["Please Contact a guide and consult local climbers before attempting to access this area."],"topos":[5,8,16],"safe":false,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207658-250","name":"Kochel Rock","description":"Located right next to Duncan's Boot, this crag contains 4 short climbs on sharp rock.","approachpics":false,"approach":["Continue straight along this path to get to Kachel Rock."],"topos":[1],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"10","warnings":{"bolt":3,"mosquitos":0,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207632-240","name":"Lap Lair","description":"Bolts in this area are not well maintained. Unsafe to climb.","approachpics":false,"approach":["Please Contact a guide and consult local climbers before attempting to access this area."],"topos":[5,8,16],"safe":false,"preApproach":["Muai Thai","Cave wall"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207668-254","name":"Low Tide Wall","description":"One of the best crag's in Railay, Low Tide can be found after a 20 minute approach along the coastline. Like the name suggests, you can only climb here at low tide. ","approachpics":false,"approach":["Follow the coast for 10-20mins."],"topos":[1],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"25","warnings":{"bolt":3,"mosquitos":0,"rockfall":0,"tide":4}},{"web-scraper-order":"1534207694-263","name":"Muai Thai","description":"Tucked away from the craziness of one, two, three wall, Muai Thai offers challenging and fun routes.","approachpics":true,"approach":["Head to the south end of Railay east beach. At high tide, this path can get flooded, but is still passable.","When you're almost at the cliff face, continue straight towards the beach. The big wall on your right is Muai Thai"],"topos":[1,10],"safe":true,"preApproach":[],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207692-262","name":"One-Two-Three","description":"Lower graded climbs and easy access makes this crag incredibly popular with the local climbing schools.","approachpics":true,"approach":["Continue alongside cliff face to the beach until you reach The Pinnacle, a tall rock on your left. One-Two-Three is on your right"],"topos":[1,23],"safe":true,"preApproach":["Muai Thai"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207656-249","name":"Phra-Nang Beach","description":"This is a great area","approachpics":false,"approach":["Head to the south end of railay east beach. Just before you reach the cliff face, turn right down the path.","You'll see an access point for the viewpoint and lagoon. Ignore it and keep following the main path","The path will eventually open up onto a beach.","Keep walking to your left to access the climbs."],"topos":[1,8],"safe":true,"preApproach":[],"approachTime":"10","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207653-248","name":"Pra-Nang Beach Cave","description":"Stunning beach side-views make this crag a popular destination among climbers. Masses of beach going tourists make an excellent audience for willing show ponies.","approachpics":false,"approach":["Accessable at low/medium tide. Head to the south end of Railay east beach. At high tide, this path can get flooded, but is still passable.","When you're almost at the cliff face, continue straight towards the beach.","The big wall on your left is Muai Thai, continue along the beach just past it.","The wall should now be on your right, if you've left this beach section you've gone too far."],"topos":[5,8,16],"safe":true,"preApproach":["Phra-Nang Beach"],"approachTime":"10","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207643-244","name":"Thaiwand Wall","description":"The 20 minute approach up through the jungle makes way for stunning views and some of the best climbing in Railay.","approachpics":false,"approach":["Follow the path with the cliff face on your left, this is Wee's Present Area.","Before you reach the end of the clearing, the path forks a little, keep right.","Continue along the path through the jungle.","After a couple of minutes you'll need to climb down a boulder.","After the boulder, keep to the path upwards and slightly to your left, follow the cliff face on your left as much as possible.","The path will eventually open out into the start of Thaiwand wall","Several climbs are accessed via the ladder just past the caves"],"topos":[1,7],"safe":true,"preApproach":["Cave wall"],"approachTime":"20","warnings":{"bolt":3,"mosquitos":2,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207640-243","name":"The Candlestick Area","description":"This crag is located on the trail to Thaiwand Wall. Although the approach is a little taxing you'll be rewarded with stunning views.","approachpics":false,"approach":["Accessable at low/medium tide. Head to the south end of Railay east beach. At high tide, this path can get flooded, but is still passable.","When you're almost at the cliff face, continue straight towards the beach.","The big wall on your left is Muai Thai, continue along the beach just past it.","The wall should now be on your right, if you've left this beach section you've gone too far."],"topos":[1],"safe":true,"preApproach":["Cave wall","Thaiwand Wall"],"approachTime":"25","warnings":{"bolt":3,"mosquitos":1,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207680-260","name":"The Defile","description":"This is a great area","approachpics":false,"approach":["Follow the cliff around towards the water. The area is cut off during high tide.","The area opens out into a large canyon, with a cave system on your left. The Defile is straight ahead."],"topos":[1],"safe":true,"preApproach":["Phra-Nang Beach"],"approachTime":"15","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207673-256","name":"The Keep","description":"The rebolting of all routes on this crag has meant that this long kept secret has become a popular destination for climbers.","approachpics":false,"approach":["Continue up this path, with rock faces on your left and right.","Keep Going to the top","There is a fixed rope and rap station you can use to get down and over the ravine, the wall ahead and to your right is the Keep."],"topos":[1,4],"safe":true,"preApproach":["Muai Thai","One-Two-Three","Duncans Boot"],"approachTime":"20","warnings":{"bolt":2,"mosquitos":3,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207689-261","name":"The Pinnacle","description":"This small, unassuming rock face is located directly opposite One, Two, Three wall. ","approachpics":false,"approach":[],"topos":[1],"safe":true,"preApproach":["Muai Thai","One-Two-Three"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207648-246","name":"Tum Choee [Escher World]","description":"Located at the end of Pra-Nang beach and up a small jungle path, Escher wall offers interesting climbing on polished rock.","approachpics":false,"approach":["Continue walking to the end of the beach.","Where the rock meets the jungle, there's a path leading into the forest","Follow the path in.","Continue forward.","At one point it will open into a clearing on your left, ignore the clearing and keep to the path going straight ahead","You'll get to a rocky path up. Escher wall is straight ahead."],"topos":[1,3,6,10,14,16],"safe":true,"preApproach":["Phra-Nang Beach"],"approachTime":"30","warnings":{"bolt":2,"mosquitos":1,"rockfall":0,"tide":1}},{"web-scraper-order":"1534207646-245","name":"Universe Wall","description":"Bolts in this area are not well maintained. Unsafe to climb.","approachpics":false,"approach":["Please Contact a guide and consult local climbers before attempting to access this area."],"topos":[5,8,16],"safe":false,"preApproach":["Muai Thai","Cave wall"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":0,"rockfall":0,"tide":0}},{"web-scraper-order":"1534207637-242","name":"Wee's Present Wall","description":"A fun crag tucked away in the jungle. You'll be safe from the sun for most of the day, but be prepared to take on the hordes of mosquitos.","approachpics":false,"approach":[],"topos":[1,8,10,18],"safe":true,"preApproach":["Cave wall"],"approachTime":"5","warnings":{"bolt":2,"mosquitos":2,"rockfall":0,"tide":1}}]

