import React, { Component } from 'react';
import Wall from './Wall.js';
import ScrollTrigger from 'react-scroll-trigger';
import Waypoint from 'react-waypoint';
import mapImg from './images/railay-map.jpg';
const PhraNangBeach = require('./images/areamap/PhraNangBeach.png');
const ThaiwandWall = require('./images/areamap/ThaiwandWall.png');
const TumChoeeEscherWorld = require('./images/areamap/TumChoeeEscherWorld.png');
const DiamondCaveNorthFace = require('./images/areamap/DiamondCaveNorthFace.png');
const HappyIsland = require('./images/areamap/HappyIsland.png');
const OneTwoThree = require('./images/areamap/HappyIsland.png');
//var DuncansBoot, Cavewall, HappyIsland, HiddenWorldWall, JungleGym, JungleGymSouthFace, KochelRock, LowTideWall, MuaiThai, TheCandlestickArea, TheDefile, TheKeep, ThePinnacle, WeesPresentWall;
//DuncansBoot = HiddenWorldWall = OneTwoThree;

class Areas extends Component {

constructor(props) {
  super(props);
  this.state = { 
      img: OneTwoThree
    }
}

  onEnterViewport(imgname) {
    imgname = imgname.replace(/[^a-zA-Z ]/g, "")
    imgname = imgname.replace(/\s/g, '');


let imageurl = "OneTwoThree";

  switch(imgname) {
    case "Cavewall": imageurl = ThaiwandWall; break;
    case "DiamondCaveNorthFace": imageurl = DiamondCaveNorthFace; break;
    case "DuncansBoot": imageurl = OneTwoThree; break;
    case "HappyIsland": imageurl = HappyIsland; break;
    case "HiddenWorldWall": imageurl = OneTwoThree; break;
    case "JungleGym": imageurl = OneTwoThree; break;
    case "JungleGymSouthFace": imageurl = OneTwoThree; break;
    case "KochelRock": imageurl = OneTwoThree; break;
    case "LowTideWall": imageurl = OneTwoThree; break;
    case "MuaiThai": imageurl = OneTwoThree; break;
    case "OneTwoThree": imageurl = OneTwoThree; break;
    case "PhraNangBeach": imageurl = PhraNangBeach; break;
    case "ThaiwandWall": imageurl = ThaiwandWall; break;
    case "TheCandlestickArea": imageurl = ThaiwandWall; break;
    case "TheDefile": imageurl = PhraNangBeach; break;
    case "TheDefileExit": imageurl = PhraNangBeach; break;
    case "TheKeep": imageurl = OneTwoThree; break;
    case "ThePinnacle": imageurl = OneTwoThree; break;
    case "TumChoeeEscherWorld": imageurl = TumChoeeEscherWorld; break;
    case "WeesPresentWall": imageurl = ThaiwandWall; break;
    default: imageurl = OneTwoThree;
}


this.setState({ img: imageurl })
  };


  render() {

    const filterData = this.props.jsonData.filter(item => item.selected)
const areaData = Array.from(filterData).filter(item => item.links === this.props.areaName);
//let cragsList = [...new Set(areaData.map(item => item.links2))];
//cragsList.sort();

const areaArr = areaData.map(x => x.links2)

function compressArray(original) {
  var compressed = [];
  var copy = original.slice(0);
  for (var i = 0; i < original.length; i++) {
    var myCount = 0;  
    for (var w = 0; w < copy.length; w++) {
      if (original[i] == copy[w]) {
        myCount++;
        delete copy[w];
      }
    }
    if (myCount > 0) {
      var a = new Object();
      a.value = original[i];
      a.count = myCount;
      compressed.push(a);
    }
  }
  return compressed;
};

function compare(a,b) {
  if (a.count > b.count)
    return -1;
  if (a.count < b.count)
    return 1;
  return 0;
}

const cragsList = compressArray(areaArr).sort(compare).map(x => x.value);



    return (
      <div>
      <div className="fixedmap"><img src={mapImg} width="100%" /></div>
      <div className="fixedmap"><img src={this.state.img} width="100%" /></div>
      <div className="borders toppadding">    { 
            cragsList.map( x => <div><Waypoint onEnter={() => this.onEnterViewport(x)} topOffset='350px' bottomOffset='100px' /><Wall crags={x} key={x+"areasKey"} areaName={this.props.areaName} jsonData={this.props.jsonData}  detailData={this.props.detailData}/></div> )
          }
        </div>
        </div>
    );
  }
}

export default Areas;


