import React, { Component } from 'react';
import sportImg from './images/Sport.png';

class Routes extends Component {
  render() {

//let imgsrc = process.env.PUBLIC_URL + '/' + this.props.routeData.tags + ".png";

    return (
      <table>
<tbody className={(this.props.routeData.selected ? 'show' : 'hidden')}>
  <tr>
      <td className="title"><span className="text">{this.props.routeData.number} {this.props.routeData.Name}</span></td>
      <td className="meta bolts"> {this.props.routeData.bolts}<img alt={this.props.routeData.tags} src={sportImg} /></td>
      <td className="meta">{this.props.routeData.length}m</td>
      <td className="meta">{this.props.routeData.grade}</td>
    </tr>
      <tr>
      <td colSpan="4" className="desc">{this.props.routeData.description}</td>

    </tr>

    </tbody>
</table>
     
    );
  }
}

export default Routes;


