import React, { Component } from 'react';
import Routes from './routes.js';
import { Link } from 'react-router-dom'
import Slider from "react-slick";
import Waypoint from 'react-waypoint';
import backImg from './images/back.png';

var images = require.context('./images/access', false, /\.jpg$/);
var images2 = require.context('./images/topos', false, /\.jpg$/);
var images3 = require.context('./images/warnings', false, /\.png$/);

class Crags extends Component {

constructor(props) {
  super(props);
  this.state = { 
      img: images2("./LowTideWall1.jpg")
    }
}

  onEnterViewport(climb, topos, url) {

if (topos.includes(climb.number)) {
//let imageurl = process.env.PUBLIC_URL + "/topos/" + url + climb.number + ".jpg";
let imageurl = images2("./"+ url + climb.number +".jpg");
this.setState({ img: imageurl })

}
else if (topos.includes(climb.number+1))
{
  let y = topos[topos.findIndex(k => k==climb.number+1)-1]

let imageurl = process.env.PUBLIC_URL + "/topos/" + url + y + ".jpg";
this.setState({ img: imageurl })
}

  };

  render() {

    function compare(a,b) {
  if (a.number < b.number)
    return -1;
  if (a.number > b.number)
    return 1;
  return 0;
}

const cragURL = decodeURIComponent(this.props.match.params.crag);
//find all area data
let areaData = Array.from(this.props.jsonData).filter(item => item.links2 === cragURL);


let areaData2 = this.props.detailData.filter(function (n){
        return n.name=== cragURL;
    })[0];


let shortcrag = cragURL.replace(/[^a-zA-Z ]/g, "")
    shortcrag = shortcrag.replace(/\s/g, '');

let slides = [];

const approach = areaData2.approach;
const approachtime = areaData2.approachTime;
const topos = areaData2.topos;
const preApproach = areaData2.preApproach;
const deetsData = this.props.detailData;

// assemble slides from preapproach
if (preApproach.length > 0 ){
preApproach.forEach(function(x) {
  let tempApp = deetsData.filter(function (n){
        return n.name=== x;
    })[0];

console.log(tempApp)
let y = tempApp.approach;
let shortx = x.replace(/[^a-zA-Z ]/g, "")
    shortx = shortx.replace(/\s/g, '');

let slides1 = y.map((item, i) => <div><img src={images("./"+shortx+(i+1)+".jpg")} width="100%" /><p>{y[i]}</p></div>);
slides = slides.concat(slides1);
});
}

// assemble slides from approach

let slides1 = approach.map((item, i) => <div><img src={images("./"+shortcrag+(i+1)+".jpg")} width="100%" /><p>{approach[i]}</p></div>);
slides = slides.concat(slides1);

const routeData = Array.from(areaData).filter(item => item.links2 === cragURL);
routeData.sort(compare);

var settings = {
      dots: false,
      arrows: true,
      infinite: false,
      speed: 500,
      slidesToShow: 1,
      slidesToScroll: 1
    };

//render warnings
function Warnings(x) {
let message = "blah";
switch(x.type) {
    case "bolt":
        message = "Unsafe bolts";
        break;
        case "tide":
        message = "High tide access";
        break;
        case "mosquitos":
        message = "Mosquitos";
        break;
    default:
        message = "blah";
}

  const danger = ["None", "Low", "Low/Moderate", "Moderate", "High", "Extreme"];

  if (x.value > 0) {
    return <tr>
<td><img alt={x.type+" warnings"} src={images3("./"+x.type+".png")} />
</td><td>
<span className="meta warnbold">{message}<br /><b className={"danger"+[x.value]}>{danger[x.value]} Risk</b></span></td>
</tr>
  }
  else return null
};

    return (
          <div className="wallContainer mobContainer">
          <div className="areaTitle">
          <Link to="../browse"><img alt="back" className="backButton" src={backImg} /></Link>
          <h2>{cragURL}</h2>
{/* <img alt="filter" className="filterButton" src={process.env.PUBLIC_URL + '/filter.png'} onClick={this.props.toggleFilters} />*/}
</div>
<div className="borders">

<table className="warntable">
<Warnings type="bolt" value={areaData2.warnings.bolt} />
<Warnings type="tide" value={areaData2.warnings.tide} />
<Warnings type="rockfall" value={areaData2.warnings.rockfall} />
<Warnings type="mosquitos" value={areaData2.warnings.mosquitos} />
</table>

<hr /><h2>Approach: {approachtime} mins</h2>
<Slider {...settings}>
        {slides}
      </Slider>
      <hr />
<h2>Climbs</h2>
</div>

<div className="fixedtopo"><img src={this.state.img} width="100%" /></div>

          { 
            routeData.map( x => <div><Waypoint onEnter={() => this.onEnterViewport(x, topos, shortcrag)} topOffset='350px' /><Routes routeData={x} key={x.Name+"routeKey"}/></div> )
          }
         
          </div>
    );
  }
}

export default Crags;


