import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import railayImg from './images/railay.jpg';
import mapImg from './images/railay-map.jpg';

class Intro extends Component {
  render() {


    return (
      <div>
<div className="introBox">
<h5>RAILAY</h5>
<h1>CLIMBING</h1>
<h1>GUIDE</h1>
</div>
<div className="borders">
<p>Railay and Tonsai have long been climbing meccas for the global climbing community. With beautiful beaches, spectacular views, top-grade routes and friendly Thai hospitality it's not hard to see why.</p>
<br />
<h2>Climbing Map</h2>
<img src={mapImg} width="100%" />
    <div className="stickyform"><form>
<p className="nomargin">Browse by grade:{'\u00A0'}{'\u00A0'} 
<select name="gradeMax" value={this.props.filterData.gradeMax}  onChange={this.props.handle}>
  <option value="4">4</option>
  <option value="5">5</option>
  <option value="6.1">6a</option>
  <option value="6.11">6a+</option>
  <option value="6.2">6b</option>
  <option value="6.21">6b+</option>
  <option value="6.3">6c</option>
  <option value="6.31">6c+</option>
  <option value="7.1">7a</option>
  <option value="7.11">7a+</option>
  <option value="7.2">7b</option>
  <option value="7.21">7b+</option>
  <option value="7.3">7c</option>
  <option value="7.31">7c+</option>
  <option value="8.1">8a</option>
  <option value="8.11">8a+</option>
  <option value="8.2">8b</option>
</select>
{'\u00A0'}{'\u00A0'}
      <Link to={{ pathname: 'browse', state: { foo: 'not'} }}>
    <button className="mainButton">Next</button></Link>
     </p> </form></div>
<br />
<h2>Best time to come</h2>
<p>Thailand's weather can be split into two seasons - wet and dry. </p>

<p>November - April is dry season, which means blues skies and low humidity. Dry season is high season, so be prepared to wade through the tourists crowds and pay a little bit more for your accomodation. </p>

<p>May - October is wet season and although you will not be contending with the tourist crowds you will be dealing with unpredictable weather and humidity. You can expect one or two downpours each day, however they are often short-lived so your day of climbing is not entirely lost.
</p>
<br />
<h2>Getting there</h2>
<p>Railay is located on the coast between Krabi Town and Ao Nang. Although it is part of the mainland, it is only accessible by boat. Longtail boat services depart from nearby piers: from Krabi Town and Ao Nam Mao, arriving on the east side of the peninsula; and from Ao Nang and Ton Sai, arriving on the west side. The cost of a longtail trip is 100 THB. The boats operate during daylight hours and will leave each pier when they are full.</p>


<img src={railayImg} width="100%" /><br />
<br />
<h2>Mosquitos</h2>
<p>Dengue fever is not an uncommon occurrence in Railay. If you planning a day out of the crag be prepared with mosquito repellant and mosquito coils. If you are travelling in rainy season, it's not a bad idea to light a contained fire at the base of your climb. Coconut husks are ideal for slow burning, smokey fires.</p>


<br />
<h2>Tides</h2>
<p>After spending a few days in Railay you will quickly learn that most of your climbing plans will be determined by the tides. Some crags in Railay are only accessible by foot during low tide. The best option is to download a tide monitoring app or consult Google and plan your movements to and from the crag accordingly.</p>


<br />
<h2>Food + Water</h2>
<p>The steady stream of tourists to Railay means that restaurant options are aplenty. Food is fresh and well-priced and establishments are clean. </p>

<p>The water is not save to drink in Railay. There are several minimarts throughout Railay so be sure to stock up your supplies each day.
</p>

<br />
<h2>Accomodation</h2>
<p>There is no shortage of accomodation options in Railay. Unfortunately you will be paying more for your stay than your friends in neighbouring Tonsai, but 24 hour electricity, free breakfasts and fast wi-fi certainly make up for this. Be sure to shop around for accomodation options to suit your budget.</p>


<br />
<h2>Gear</h2>
<p>A 60m rope will suffice for most climbs in Railay with most routes coming in below the 28m mark.
12 quick draws, 2 long draws, personal safety slings, a belay device, a mat for your rope and climbing shoes should be enough to get you on your way. Thailand's humdity and heat will leave you super sweaty, so be sure to bring a tonne of chalk.</p>


<br />
<h2>Grades</h2>
<p>The limestone walls of Railay make for challenging, fun 3D climbing. With routes starting from grade 5, there is something for even the most novice climber. Many of lower graded routes in Railay are quite polished owing to their popularity.</p>

<br />
<h2>Community</h2>
<p>If you're travelling solo to Railay you'll have no trouble finding a climbing partner. The best option is to turn up to a crag and start chatting to your fellow climbers. You can also suss out potential partners at your accomodation or in town. It's pretty easy to spot a climber in Railay. </p>



        </div>
        </div>
    );
  }
}

export default Intro;


